import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import {
  Input,
  Button,
  Message,
  Upload
} from 'element-ui'
Vue.use(Input);
Vue.use(Button);
Vue.use(Upload);
Vue.config.productionTip = false
Vue.prototype.$message = Message
Vue.prototype.$axios = axios

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')