<template>
  <div class="warrper">
    <el-input class="small" placeholder="IP Address" v-model="ip"></el-input><br />
    <el-input class="small" placeholder="Username" v-model="username"></el-input> <br />
    <el-input class="small" type="password" placeholder="Password" v-model="password"></el-input><br/>
    <el-button class="small" type="primary" @click="check">校验</el-button> <br/>
    <el-upload v-model="file" :on-change="getFile" :show-file-list="false" :auto-upload="false">
      <el-button class="small" type="primary">选择文件</el-button><br/>
      </el-upload>
      <div v-if="file">
        {{ file.name }}
      </div>
	  <el-button class="small" type="primary" @click="upload">上传</el-button><br>
    <el-input class="small" placeholder="namespaces" v-model="namespaces"></el-input> <br/>
    <el-button class="small" type="primary" @click="getPods">获取空间下的pod</el-button> <br/>
    <el-input class="small" placeholder="container_name" v-model="container_name"></el-input><br/>
    <el-input class="small" placeholder="container_path" v-model="container_path"></el-input><br/>
    <el-button class="small" type="primary" @click="map">映射</el-button><br/>
    <h1>{{ result }}</h1>
    <h2 v-html="pod.replace(/\n/g, '<br>')"></h2>
  </div>
</template>
<script>
export default {
  name: 'HomeView',
  components: {
  },
  data() {
    return {
      ip: "",
      username: "",
      password: "",
      file: "",
      namespaces: "",
      container_name: "",
      container_path: "",
      result: "",
      pod: ""
    };
  },
  methods: {
    check() {
      if (!this.ip || !this.username || !this.password) {
        this.$message.error('请输入IP、用户名、密码')
        return
      }
      this.$axios.get('/check', {
        params: {
          ip: this.ip,
          username: this.username,
          password: this.password
        }
      }).then(res => {
        if (res.status === 200) {
          this.result = res.data
        } else {
          this.result = res.data
        }
      })
    },
    async upload() {
        const ip = this.ip;
        const username = this.username;
        const password = this.password;
        const file = this.file;
        // 创建FormData对象，并序列化
        const formData = new FormData();
        formData.append("ip", ip);
        formData.append("username", username);
        formData.append("password", password);
        formData.append("file", file);

        try {
          const res = await this.$axios.post("/upload", formData);
          this.result = res.data;
        } catch (error) {
          this.result = error.response.data;
        }
      },
      getFile(file) {
        this.file = file.raw;
      },
    async getPods() {
      if (!this.namespaces) {
        this.$message.error('请输入命名空间');
        return;
      }
      this.$axios.get('/pod', {
        params: {
          ip: this.ip,
          username: this.username,
          password: this.password,
          namespaces: this.namespaces
        }
      }).then(res => {
        if (res && res.data) {
           this.pod = res.data.replace(/\n/g, ' <br/>');
        }
      }).catch(error => {
        console.log(error)
          this.pod  = error.response.data;
      });
    },
    async map () {
      if (!this.container_path) {
        this.$message.error('请输入容器路径');
        return;
      }

      if (!this.container_name) {
        this.$message.error('请输入容器名字');
        return;
      }

      if (!this.namespaces) {
        this.$message.error('请输入命名空间');
        return;
      }

      const formData = new FormData();
      formData.append("ip", this.ip);
      formData.append("username", this.username);
      formData.append("password", this.password);
      formData.append("namespaces", this.namespaces);
      formData.append("container_name", this.container_name);
      formData.append("container_path", this.container_path);
      formData.append("file", this.file);

      try {
        const response = await this.$axios.post('/map', formData);
        this.result = response.data;
      } catch (error) {
        this.result = error.response.data;
      }
    }
  }
}
</script>
<style scoped>
	  .warrper {
	    height: 100%;
		align-items: center;
		text-align: center;
		justify-content: center;
	  }
	   .small {
	    width: 200px;
		display: inline-block;
		margin-top: 3px;
	  }
	  #pod {
	    white-space: pre-wrap;
	    text-align: left;
	    padding: 150px;
	  }
</style>